let sex = ['restrict','male','female']
let emotion = ['married','signora','bachelordom']
let job = ['IT','tearcher','husbandman']
import mpvuePicker from "../../components/mpvue-picker/mpvuePicker.vue"
export default {
	compontents:{
		mpvuePicker
	},
	data() {
		return {
			userpic:'../../static/图片素材/demo6.jpg',
			username:'fgf',
			sex:'不限',
			emotion:'未婚',
			job:'IT',
			birthday:'93-11-18',
			cityPickerValueDefault: [0, 0, 1],
			pickerText: '江苏-徐州-睢宁',
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onBackPress() {
	  if (this.$refs.mpvueCityPicker.showPicker) {
		this.$refs.mpvueCityPicker.pickerCancel();
		return true;
	  }
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	},
	methods: {
		// 三级联动
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.pickerText = e.label;
		},
		submit(){
			
		},
		// 修改头像
		changeimg(){
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: [ 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    //sourceType: ['album'], //从相册选择
			    success:(res)=>{
			        console.log(JSON.stringify(res.tempFilePaths));
					this.userpic = res.tempFilePaths
			    }
			});
		},
		// 单例选择
		changeOne(val){
			let arr = []
			switch(val){
				case 'sex':
				arr = sex
				break
				case 'emotion':
				arr = emotion
				break
				case 'job':
				arr = job
				break
			}
			uni.showActionSheet({
				itemList:arr,
				success:(res)=>{
					// console.log(res.tapIndex + 1)
					switch(val){
						case 'sex':
						this.sex=arr[res.tapIndex]
						break
						case 'emotion':
						this.emotion=arr[res.tapIndex]
						break
						case 'job':
						this.job = arr[res.tapIndex]
						break
					}
				}
			})
		},
		getDate(type) {
		            const date = new Date();
		            let year = date.getFullYear();
		            let month = date.getMonth() + 1;
		            let day = date.getDate();
		
		            if (type === 'start') {
		                year = year - 60;
		            } else if (type === 'end') {
		                year = year + 2;
		            }
		            month = month > 9 ? month : '0' + month;;
		            day = day > 9 ? day : '0' + day;
		            return `${year}-${month}-${day}`;
		},
		// 修改生日
		bindDateChange(e){
			this.birthday = e.target.value
		},
	}
}
